export default {
    data(){
        return{
            scroll: null
        }
    },
    methods:{
        onScroll (pos) {
            this.scroll = pos.left;
        },
    },
    watch:{
        scroll(){
            if(this.scroll < -50){
                this.$router.push('/home')
            }
        }
    }
}



